        <div class="title">
            <div class="titleTab">
                <div @click="$router.push({path:'/zizhi'})">
                    公司资质
                </div>
                <div @click="$router.push({path:'/chanpin'})">
                    产品介绍
                </div>
                <div @click="$router.push({path:'/fazhan'})">
                    发展规划
                </div>
                <div @click="$router.push({path:'/lianxi'})">
                    联系我们
                </div>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
    data() {
        return {
        };
    },
    created:function(){
    },
    mounted:function(){
    },
    watch:{
    },
    methods:{
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.logo{
    width: 1360px;
    height: 137px;
    margin: 0 auto;
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
