                justify-content: space-between;
            }
        }
    }
}
.title{
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    height: 44px;
    line-height: 44px;
    margin: 20px 0;
    text-align: center;
    background: #FFAD01;
    .titleTab{
        width: 1200px;
        margin: 0 auto;
        display: flex;
        div{
            margin: 0 30px;
            cursor:pointer;
        }
    }
}
</style>


